<template>
  <div
    id="productModal"
    ref="productModal"
    class="modal fade"
    tabindex="-1"
    aria-labelledby="productModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-xl">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 id="productModalLabel" class="modal-title">
            <span v-if="newProduct"></span>
            <span v-else></span>
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="mb-1">
                <div class="form-group my-2">
                  <label for="imageUrl"></label>
                  <input
                    type="text"
                    class="form-control"
                    placeholder=""
                    v-model="tempProduct.imageUrl"
                  />
                </div>
                <div class="form-group my-2">
                  <label for="imageUrl"></label>
                  <input
                    type="file"
                    class="form-control"
                    ref="fileInput"
                    @change="uploadFile"
                  />
                </div>
                <img class="img-fluid" :src="tempProduct.imageUrl" alt="" />
              </div>
              <div v-if="Array.isArray(tempProduct.imagesUrl)">
                <button
                  class="btn btn-outline-primary btn-sm d-block w-100"
                  @click="addPicture"
                >
                  
                </button>
              </div>
              <div v-else>
                <button
                  class="btn btn-outline-primary btn-sm d-block w-100"
                  @click="newPicture"
                >
                  
                </button>
              </div>
              <div>
                <button
                  class="btn btn-outline-danger btn-sm d-block w-100"
                  @click="delPicture"
                >
                  
                </button>
              </div>
              <div v-if="Array.isArray(tempProduct.imagesUrl)">
                <img
                  class="img-fluid my-2"
                  v-for="item in tempProduct.imagesUrl"
                  :key="item"
                  :src="item"
                  alt=""
                />
              </div>
            </div>
            <div class="col-sm-8">
              <div class="form-group">
                <label for="title"></label>
                <input
                  id="title"
                  type="text"
                  class="form-control"
                  placeholder=""
                  v-model="tempProduct.title"
                />
              </div>

              <div class="row">
                <div class="form-group col-md-6">
                  <label for="category"></label>
                  <input
                    id="category"
                    type="text"
                    class="form-control"
                    placeholder=""
                    v-model="tempProduct.category"
                  />
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input
                    id="unit"
                    type="text"
                    class="form-control"
                    placeholder=""
                    v-model="tempProduct.unit"
                  />
                </div>
              </div>

              <div class="row">
                <div class="form-group col-md-6">
                  <label for="origin_price"></label>
                  <input
                    id="origin_price"
                    type="number"
                    min="0"
                    class="form-control"
                    placeholder=""
                    v-model.number="tempProduct.origin_price"
                  />
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input
                    id="price"
                    type="number"
                    min="0"
                    class="form-control"
                    placeholder=""
                    v-model.number="tempProduct.price"
                  />
                </div>
              </div>
              <hr />

              <div class="form-group">
                <label for="description"></label>
                <textarea
                  id="description"
                  type="text"
                  class="form-control"
                  placeholder=""
                  v-model="tempProduct.description"
                >
                </textarea>
              </div>
              <div class="form-group">
                <label for="content"></label>
                <textarea
                  id="description"
                  type="text"
                  class="form-control"
                  placeholder=""
                  v-model="tempProduct.content"
                >
                </textarea>
              </div>
              <div class="form-group">
                <div class="form-check">
                  <input
                    id="is_enabled"
                    class="form-check-input"
                    type="checkbox"
                    :true-value="1"
                    :false-value="0"
                    v-model.number="tempProduct.is_enabled"
                  />
                  <label class="form-check-label" for="is_enabled"
                    ></label
                  >
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-outline-secondary"
            data-bs-dismiss="modal"
          >
            
          </button>
          <button
            type="button"
            class="btn btn-primary"
            @click="this.$emit('productStatus', tempProduct)"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import modalMixin from '../mixins/modalMixins';

export default {
  props: {
    product: {
      type: Object,
      default() {
        return {};
      },
    },
    newProduct: {
      type: Boolean,
      default() {
        return true;
      },
    },
  },
  data() {
    return {
      productModal: {},
      tempProduct: {},
    };
  },
  mixins: [modalMixin],
  watch: {
    product() {
      this.tempProduct = this.product;
    },
  },
  methods: {
    newPicture() {
      this.tempProduct.imagesUrl = [];
      this.tempProduct.imagesUrl.push(this.tempProduct.imageUrl);
      this.tempProduct.imageUrl = '';
    },
    addPicture() {
      this.tempProduct.imagesUrl.push(this.tempProduct.imageUrl);
      this.tempProduct.imageUrl = '';
    },
    delPicture() {
      this.tempProduct.imagesUrl.pop();
    },
    uploadFile() {
      const uploadFile = this.$refs.fileInput.files[0];
      const formData = new FormData();
      formData.append('file-to-upload', uploadFile);
      const url = `   ${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`;
      this.$http
        .post(url, formData)
        .then((res) => {
          console.log(res);
          if (res.data.success) {
            this.tempProduct.imageUrl = res.data.imageUrl;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
